import React, { useState } from 'react';
import { connect } from 'react-redux';
import styles from './ProductDetailsPage.module.scss';
import { getItem, updateItem, addItem } from '../../../redux/actions/getItem';
import { useMediaQuery } from 'react-responsive';
import UpperPartAdmin from './sections/UpperPart/UpperPartAdmin';
import CharacteristicIsAdmin from './sections/Characteristics/CharacteristicIsAdmin';
import AddReview from './sections/AddReview';
import ClientsPhotos from './sections/ClientsPhotos';
import Video from './sections/Video';
import { Preloader } from 'components/common/Preloader/Preloader';
import DescriptionText from './components/DescriptionText';
import { onModalOpen } from 'redux/actions/modal';
import SavedModal from 'components/common/SavedModal/SavedModal';

const ProductAdminPage = ({
  data,
  updateItem,
  getItem,
  isAdmin,
  upperPhotosArr,
  characteristicsArr,
  clientArr,
  loading,
  modalType,
  onModalOpen,
}) => {
  const { video, stars, _id, description } = data;

  const [newStars, setNewStars] = useState(stars);
  const updateStars = value => {
    setNewStars(value);
  };

  const updateVideo = async value => {
    await updateItem(_id, { video: value });
    getItem(_id);
  };

  const submitUpperPart = async values => {
    const newValues = {
      idLP: values.idLP,
      title: { ua: values.title.ua, en: values.title.en, ru: values.title.ru },
      titleLong: { ua: values.titleLong.ua, en: values.titleLong.en, ru: values.titleLong.ru },
      titleUpper: values.titleUpper,
      stars: newStars,
      price: values.price,
    };
    await updateItem(_id, newValues);
    onModalOpen('SavedModal');
  };
  const isDesktop = useMediaQuery({
    query: '(min-width: 569px)',
  });
  const isMobile = useMediaQuery({
    query: '(max-width: 568px)',
  });

  return (
    <section className={styles.wrapper}>
      {loading ? (
        <Preloader />
      ) : (
        <div className="container">
          {data && (
            <UpperPartAdmin
              photos={upperPhotosArr}
              isAdmin={isAdmin}
              onSubmit={submitUpperPart}
              initialValues={data}
              updateStars={updateStars}
              newStars={newStars}
            />
          )}

          <hr />
          <Video video={video} isAdmin={isAdmin} updateVideo={updateVideo} />

          <hr />
          <CharacteristicIsAdmin idItem={_id} photos={characteristicsArr} isAdmin={isAdmin} />

          <hr />
          <DescriptionText
            idItem={_id}
            isAdmin={isAdmin}
            _id={_id}
            description={description}
            openModal={() => onModalOpen('SavedModal')}
          />

          <hr />
          <ClientsPhotos photos={clientArr} idItem={_id} isAdmin={isAdmin} />

          <hr />
          <AddReview isMobile={isMobile} isDesktop={isDesktop} isAdmin={isAdmin} />
        </div>
      )}
      {modalType === 'SavedModal' && <SavedModal />}
    </section>
  );
};

const mapStateToProps = state => {
  return {
    loading: state.productDetails.loading,
    modalType: state.modal.modalType,
  };
};

export default connect(mapStateToProps, { updateItem, getItem, addItem, onModalOpen })(ProductAdminPage);
