import React, { useEffect } from 'react';
import styles from './AddItemModal.module.scss';
import { Button } from 'reactstrap';
import { connect } from 'react-redux';
import { onModalClose } from 'redux/actions/modal';
import { useHistory } from 'react-router-dom';
import { withTranslation } from 'react-i18next';
import { addItem } from '../../../redux/actions/getItem';
import AddItemForm from './AddItemForm';

const AddItemModal = ({ addItem, isOpen, modalType, onModalClose, totalCount, totalPrice, t }) => {
  let history = useHistory();
  useEffect(() => {
    const handleEsc = e => {
      if (e.keyCode === 27) {
        onModalClose();
      }
    };
    window.addEventListener('keydown', handleEsc);

    return () => {
      window.removeEventListener('keydown', handleEsc);
    };
  }, [onModalClose]);

  const submitUpperPart = async values => {
    const newItem = {
      idLP: values.idLP,
      title: { ua: values.title },
      titleLong: { ua: values.titleLong },
      titleUpper: { ua: values.titleUpper },
      category: values.category,
      subCategory: values.subCategory,
      discount: 'string',
      statusItems: true,
      isPopular: false,
    };
    await addItem(newItem).then(_id => history.push(`/item/${_id}`));
    onModalClose();
  };

  return (
    <>
      {modalType === 'AddItemModal' && (
        <div className={`${styles.modal} ${isOpen && styles.open} ${isOpen === false && styles.close}`}>
          <Button close className={styles.closeButton} onClick={onModalClose} />
          <div className={styles.modalContent}>
            <AddItemForm onSubmit={submitUpperPart} />
          </div>
        </div>
      )}
    </>
  );
};

const mapStateToProps = state => {
  return {
    isOpen: state.modal.isOpen,
    modalType: state.modal.modalType,
    data: state.productDetails.data,
  };
};

export default withTranslation()(connect(mapStateToProps, { onModalClose, addItem })(AddItemModal));
