import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { updateItem } from 'redux/actions/getItem';
import DescriptionSaveBtn from './DescriptionSaveBtn';
import styles from './DescriptionText.module.scss';

const DescriptionText = ({ description, _id, updateItem, openModal }) => {
  const [inputValue_ua, setInputValue_ua] = useState();
  const [inputValue_en, setInputValue_en] = useState();
  const [inputValue_ru, setInputValue_ru] = useState();

  useEffect(() => {
    !!description && !!description.ua && setInputValue_ua(description.ua);
    !!description && !!description.en && setInputValue_en(description.en);
    !!description && !!description.ru && setInputValue_ru(description.ru);
  }, [description]);
  const setChanges = async () => {
    await updateItem(_id, { description: { ua: inputValue_ua, en: inputValue_en, ru: inputValue_ru } });
    openModal();
  };
  return (
    <div className={styles.description}>
      <DescriptionSaveBtn setChanges={setChanges} language="UA" />
      <textarea value={inputValue_ua} onChange={e => setInputValue_ua(e.target.value)} />
      <DescriptionSaveBtn setChanges={setChanges} language="EN" />
      <textarea value={inputValue_en} onChange={e => setInputValue_en(e.target.value)} />
      <DescriptionSaveBtn setChanges={setChanges} language="RU" />
      <textarea value={inputValue_ru} onChange={e => setInputValue_ru(e.target.value)} />
    </div>
  );
};

export default connect(null, { updateItem })(DescriptionText);
