import React, { useEffect, useState, useCallback, useRef } from 'react';
import styles from './AdminPhone.module.scss';
import { connect } from 'react-redux';
import { getMainInfo, updateMainInfo, addMainInfo } from 'redux/actions/mainInfo';
import { Preloader } from 'components/common/Preloader/Preloader';
import { addPhoto, updatePhoto } from 'redux/actions/gallery';

const AdminPhone = ({
  phones1,
  phones2,
  phones3,
  email,
  getMainInfo,
  updateMainInfo,
  _id,
  address,
  loading,
  addMainInfo,
  addPhoto,
  updatePhoto,
  docs,
}) => {
  const getMainInfoCallback = useCallback(() => getMainInfo(), [getMainInfo]);

  useEffect(() => {
    getMainInfoCallback();
  }, [getMainInfoCallback]);

  const findOffer = !!docs && docs.find(doc => doc.category === 'offer');
  const findTerm = !!docs && docs.find(doc => doc.category === 'term');
  const newOfferId = !!findOffer && findOffer._id;
  const newTermId = !!findTerm && findTerm._id;

  const [editing, setEditing] = useState(false);
  const [filesEditing, setFilesEditing] = useState(false);
  const [firstPhone, setFirstPhone] = useState();
  const [secondPhone, setSecondPhone] = useState();
  const [thirdPhone, setThirdPhone] = useState();
  const [newEmail, setNewEmail] = useState();
  const [newAddress_ua, setNewAddress_ua] = useState();
  const [newAddress_en, setNewAddress_en] = useState();
  const [newAddress_ru, setNewAddress_ru] = useState();
  const [offerContract, setOfferContract] = useState();
  const [termsOfUse, setTermsOfUse] = useState();
  const [isValid, setValid] = useState(false);
  const phone1ref = useRef();
  const phone2ref = useRef();
  const phone3ref = useRef();
  useEffect(() => {
    if (phones1 || phones2 || phones3 || email || address) {
      setFirstPhone(phones1);
      setSecondPhone(phones2);
      setThirdPhone(phones3);
      setNewEmail(email);
      setNewAddress_ua(address.ua);
      setNewAddress_en(address.en);
      setNewAddress_ru(address.ru);
    }
  }, [phones1, phones2, phones3, email, address]);

  const setUpdate = () => {
    const data = {
      phones1: firstPhone,
      phones2: secondPhone,
      phones3: thirdPhone,
      email: newEmail,
      address: {
        ua: newAddress_ua,
        en: newAddress_en,
        ru: newAddress_ru,
      },
    };

    const newData = {
      phones1: firstPhone,
      phones2: secondPhone,
      phones3: thirdPhone,
      email: newEmail,
      'address.ua': newAddress_ua,
      'address.en': newAddress_en,
      'address.ru': newAddress_ru,
      id: _id,
    };
    _id ? updateMainInfo(newData) : addMainInfo(data);
  };

  const addOfferContract = () => {
    const data = {
      idItem: _id,
      category: 'offer',
      photo: offerContract,
    };
    const newData = {
      id: newOfferId,
      photo: offerContract,
    };
    newOfferId ? updatePhoto(newData) : addPhoto(data);
  };

  const addTermsOfUse = () => {
    const data = {
      idItem: _id,
      category: 'term',
      photo: termsOfUse,
    };
    const newData = {
      id: newTermId,
      photo: termsOfUse,
    };
    newTermId ? updatePhoto(newData) : addPhoto(data);
  };

  const onOfferSelect = async e => {
    setOfferContract(e.target.files[0]);
  };

  const onTermsSelect = async e => {
    setTermsOfUse(e.target.files[0]);
  };
  const validateInput = e => {
    let errors = {};
    let values = {};
    if (
      /^\+380\d{3}\d{2}\d{2}\d{2}$/i.test(e.target.value) ||
      /^380\d{3}\d{2}\d{2}\d{2}$/i.test(e.target.value) ||
      /^80\d{3}\d{2}\d{2}\d{2}$/i.test(e.target.value) ||
      /^0\d{3}\d{2}\d{2}\d{2}$/i.test(e.target.value) ||
      e.target.value === ''
    ) {
      setValid(true);
      values[e.target.name] = e.target.value;
      if (values.hasOwnProperty('phone1')) {
        setFirstPhone(values.phone1);
      }
      if (values.hasOwnProperty('phone2')) {
        setSecondPhone(values.phone2);
      }
      if (values.hasOwnProperty('phone3')) {
        setThirdPhone(values.phone3);
      }
    } else {
      values[e.target.name] = e.target.value;
      errors[e.target.name] = false;
      setValid(errors);
    }
  };
  const submitChanges = e => {
    setEditing(false);
    setUpdate();
    e.preventDefault();
  };

  const submitFiles = e => {
    setFilesEditing(false);
    addOfferContract();
    addTermsOfUse();
    e.preventDefault();
  };

  return (
    <>
      {loading ? (
        <Preloader />
      ) : (
        <div className={styles.wrapper}>
          {!editing ? (
            <div className={styles.nonEditingBlock}>
              <div className={styles.textBlock}>
                <p> 1</p>
                <h3>{phones1}</h3>
                <p> 2</p>
                <h3>{phones2}</h3>
                <p> 3</p>
                <h3>{phones3}</h3>
                <p>Email</p>
                <h3>{email}</h3>
                <p></p>
                <h3>{address && address.ua}</h3>
                <h3>{address && address.en}</h3>
                <h3>{address && address.ru}</h3>
              </div>

              <div className={styles.buttonBlock}>
                <button className={styles.button} onClick={() => setEditing(true)}>
                  
                </button>
              </div>
            </div>
          ) : (
            <div className={styles.editingBlock}>
              <form onSubmit={submitChanges} className={styles.editingForm}>
                <div>
                  -1
                  <br />
                  <input
                    ref={phone1ref}
                    placeholder=" "
                    type="text"
                    name="phone1"
                    defaultValue={firstPhone}
                    onChange={validateInput}
                  />
                  {!!phone1ref.current && isValid.hasOwnProperty(phone1ref.current.name) && (
                    <span style={{ color: 'red' }}>  </span>
                  )}
                </div>

                <div>
                  -2
                  <br />
                  <input
                    ref={phone2ref}
                    placeholder=" "
                    type="text"
                    name="phone2"
                    defaultValue={secondPhone}
                    onChange={validateInput}
                  />
                  {!!phone2ref.current && isValid.hasOwnProperty(phone2ref.current.name) && (
                    <span style={{ color: 'red' }}>  </span>
                  )}
                </div>

                <div>
                  -3
                  <br />
                  <input
                    ref={phone3ref}
                    placeholder=" "
                    type="text"
                    name="phone3"
                    defaultValue={thirdPhone}
                    onChange={validateInput}
                  />
                  {!!phone3ref.current && isValid.hasOwnProperty(phone3ref.current.name) && (
                    <span style={{ color: 'red' }}>  </span>
                  )}
                </div>

                <div>
                  Email
                  <br />
                  <input
                    placeholder="email"
                    type="text"
                    defaultValue={newEmail}
                    onChange={e => setNewEmail(e.target.value)}
                  />
                </div>

                <div className={styles.regNumber}>
                  
                  <br />
                  <input
                    placeholder="ukr"
                    type="text"
                    defaultValue={newAddress_ua}
                    onChange={e => setNewAddress_ua(e.target.value)}
                  />
                  <input
                    placeholder="eng"
                    type="text"
                    defaultValue={newAddress_en}
                    onChange={e => setNewAddress_en(e.target.value)}
                  />
                  <input
                    placeholder="ru"
                    type="text"
                    defaultValue={newAddress_ru}
                    onChange={e => setNewAddress_ru(e.target.value)}
                  />
                </div>

                <input className={styles.button} type="submit" value="save" />
              </form>
            </div>
          )}
          <div className={styles.filesInputBlock}>
            {!filesEditing ? (
              <button className={styles.button} onClick={() => setFilesEditing(true)}>
                 
              </button>
            ) : (
              <>
                <form onSubmit={submitFiles} className={styles.editingForm}>
                  <div>
                    <label> :</label>
                    <input type="file" onChange={onOfferSelect} required />
                  </div>

                  <div>
                    <label> :</label>
                    <input type="file" onChange={onTermsSelect} required />
                  </div>

                  <input className={styles.button} type="submit" value="save" />
                </form>
              </>
            )}
          </div>
        </div>
      )}
    </>
  );
};

const mapStateToProps = state => {
  return {
    phones1: state.mainInfo.data.phones1,
    phones2: state.mainInfo.data.phones2,
    phones3: state.mainInfo.data.phones3,
    email: state.mainInfo.data.email,
    _id: state.mainInfo.data._id,
    address: state.mainInfo.data.address,
    loading: state.mainInfo.loading,
    docs: state.gallery.data,
  };
};

export default connect(mapStateToProps, { getMainInfo, updateMainInfo, addMainInfo, addPhoto, updatePhoto })(
  AdminPhone
);
