import { SET_ITEM, SET_ITEM_REQUEST, SET_ITEM_FAILURE } from '../types/getItem';
import { itemAPI } from '../../api/api';

export const setItem = data => ({ type: SET_ITEM, data });

export const setItemRequest = () => {
  return {
    type: SET_ITEM_REQUEST,
  };
};

export const setItemFailure = error => {
  return {
    type: SET_ITEM_FAILURE,
    error,
  };
};

export const getItem = productId => {
  return async dispatch => {
    dispatch(setItemRequest());

    try {
      const data = await itemAPI.getDataItem(productId);
      dispatch(setItem(data[0]));
    } catch (error) {
      dispatch(setItemFailure(error));
    }
  };
};

export const addItem = newData => {
  return async dispatch => {
    dispatch(setItemRequest());

    try {
      const data = await itemAPI.addItem(newData);
      dispatch(setItem(data));
      return data._id;
    } catch (error) {
      dispatch(setItemFailure(error));
    }
  };
};

export const updateItem = (_id, newData) => {
  return async dispatch => {
    try {
      await itemAPI.updateItem(_id, newData);
    } catch (error) {}
  };
};
