import React, { useState, useLayoutEffect } from 'react';
import { connect } from 'react-redux';
// import ClientCarousel from 'components/pages/ProductDetailsPage/components/ClientCarousel';
import CommentForm from 'components/common/CommentForm';
import UpperPartMobile from './UpperPartMobile';
import AddToCartModal from 'components/common/AddToCartModal/AddToCartModal';
import Backdrop from 'components/common/Backdrop/Backdrop';

import styles from './ProductDetailsPage.module.scss';
import { useMediaQuery } from 'react-responsive';
import UpperPart from './sections/UpperPart/UpperPartIsNoAdmin';
import CharacteristicIsNoAdmin from './sections/Characteristics/CharacteristicIsNoAdmin';
import Reviews from './sections/Reviews';
import ClientsPhotos from './sections/ClientsPhotos';
import Video from './sections/Video';
import { Preloader } from 'components/common/Preloader/Preloader';
import { createClient, cleanCart } from 'redux/actions/cart';
import OrderConfirmModal from 'components/common/OrderConfirmModal/OrderConfirmModal';
import { withTranslation } from 'react-i18next';
import ConfirmedModal from 'components/common/ConfirmedModal/ConfirmedModal';
// import DescriptionText from './components/DescriptionText';

const ProductClientPage = ({
  data,
  isAdmin,
  upperPhotosArr,
  characteristicsArr,
  clientArr,
  loading,
  modalType,
  createClient,
  cleanCart,
  i18n,
  t,
}) => {
  const [openForm, setOpenForm] = useState(false);

  const { idLP, title, price, reviews, _id, video, description } = data;

  const sendData = {
    idLP,
    title,
    _id,
    price,
    photo: !!upperPhotosArr && !!upperPhotosArr[0] && upperPhotosArr[0].photo,
  };

  const isDesktop = useMediaQuery({
    query: '(min-width: 569px)',
  });
  const isMobile = useMediaQuery({
    query: '(max-width: 568px)',
  });
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <section className={styles.wrapper}>
      {loading ? (
        <Preloader />
      ) : (
        <div className="container">
          {isMobile && <UpperPartMobile data={data} photos={upperPhotosArr} />}
          {isDesktop && <UpperPart data={data} photos={upperPhotosArr} />}

          {video ? (
            <>
              <hr />
              <Video idItem={_id} video={video} isAdmin={isAdmin} />
            </>
          ) : null}

          <hr />
          <CharacteristicIsNoAdmin photos={characteristicsArr} />
          {isMobile && description && (
            <div className={styles.descriptionTextMobile}>
              {i18n.language === 'uk' && description.ua}
              {i18n.language === 'ru' && description.ru}
              {i18n.language === 'en' && description.en}
            </div>
          )}

          <hr />
          <Reviews reviews={reviews} isAdmin={isAdmin} data={sendData} _id={_id} isMobile={isMobile} />

          {clientArr && clientArr.length ? (
            <>
              <hr />
              <ClientsPhotos photos={clientArr} idItem={_id} isAdmin={isAdmin} />
            </>
          ) : null}

          <hr />
          {/* ADD_REVIEW_SECTION */}
          {isMobile &&
            (openForm ? (
              <div>
                <CommentForm _id={_id} />
              </div>
            ) : (
              <button onClick={() => setOpenForm(true)} className={styles.reviewBtn}>
                {t('commentForm.write')}
              </button>
            ))}
          {isDesktop && !isAdmin && (
            <div>
              <CommentForm _id={_id} />
            </div>
          )}
        </div>
      )}
      {modalType === 'AddToCart' && <AddToCartModal />}
      {modalType === 'ConfirmOrder' && <OrderConfirmModal createClient={createClient} cleanCart={cleanCart} />}
      <ConfirmedModal />
      <Backdrop />
    </section>
  );
};

const mapStateToProps = state => {
  return {
    loading: state.productDetails.loading,
    modalType: state.modal.modalType,
  };
};

export default withTranslation()(connect(mapStateToProps, { createClient, cleanCart })(ProductClientPage));
