import React from 'react';
import styles from './AddItemModal.module.scss';
import { reduxForm, Field, formValueSelector } from 'redux-form';
import { connect } from 'react-redux';

let AddItemForm = ({ handleSubmit, onModalClose, categories, category, t }) => {
  const categoriesArr = !!categories && categories.map(item => <option value={item._id}>{item.name.ua}</option>);
  const subCategoriesArr =
    !!category &&
    categories
      .find(el => el._id === category)
      .subCategorys.map(item => (
        <option value={item._id} key={item._id}>
          {item.name.ua}
        </option>
      ));

  return (
    <div className={styles.modalMain}>
      <form onSubmit={handleSubmit}>
        <div className={styles.headerTitleDiv}>
          <h2 className={styles.headerTitle}> </h2>
        </div>
        <div className={styles.description}>
          <label>ID   LP-CRM:</label>
          <Field name="idLP" placeholder=" " component="input" type="number" className={styles.cardTitle} />
        </div>
        <div className={styles.description}>
          <label>  UA:</label>
          <Field placeholder=" " name="title" component="input" type="text" className={styles.cardTitle} />
          <div>
            <label>  UA:</label>
            <Field
              placeholder=" "
              name="titleLong"
              component="input"
              type="text"
              className={styles.cardTitle}
            />
          </div>
          <div>
            <label>   UA:</label>
            <Field
              placeholder=" "
              name="titleUpper"
              component="input"
              type="text"
              className={styles.cardTitle}
            />
          </div>
          <div className={styles.category}>
            <label> :</label>
            <Field name="category" component="select">
              <option></option>
              {categoriesArr}
            </Field>
          </div>
          <div className={styles.category}>
            <label> :</label>
            <Field name="subCategory" component="select">
              <option></option>
              {subCategoriesArr}
            </Field>
          </div>
        </div>
        <div className={styles.saveBtn}>
          <button type="submit" className={styles.buyButton}>
            
          </button>
        </div>
      </form>
    </div>
  );
};

const mapStateToProps = (state, props) => ({
  status: state.gallery.status,
  categories: state.categories.data,
});

const selector = formValueSelector('addItemForm');
AddItemForm = connect(state => {
  const category = selector(state, 'category');
  return {
    category,
  };
})(AddItemForm);

export default connect(
  mapStateToProps,
  {}
)(
  reduxForm({
    form: 'addItemForm',
    enableReinitialize: true,
  })(AddItemForm)
);
