import { SET_CATEGORIES, SET_CATEGORIES_REQUEST, SET_CATEGORIES_FAILURE } from 'redux/types/categories';

const initialState = {
  data: [],
  error: '',
  loading: false,
};

const categoriesReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CATEGORIES:
      return {
        ...state,
        data: action.data,
        loading: false,
      };

    case SET_CATEGORIES_REQUEST:
      return {
        ...state,
        loading: true,
      };

    case SET_CATEGORIES_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.error,
      };

    default:
      return state;
  }
};

export default categoriesReducer;
