import React from 'react';
import styles from './Characteristics.module.scss';
import PhotoItem from 'components/pages/ProductDetailsPage/components/PhotoItem';
import AddPhotoButton from '../../components/AddPhotoButton';

const CharacteristicIsAdmin = ({ idItem, photos, isAdmin }) => {
  return (
    <div>
      <div className={styles.titleWrapper}>
        <h2></h2>
        <AddPhotoButton idItem={idItem} category="characteristics" />
      </div>
      {!!photos &&
        photos.map(item => (
          <PhotoItem photo={item.photo} key={item._id} idItem={idItem} id={item._id} isAdmin={isAdmin} />
        ))}
    </div>
  );
};

export default CharacteristicIsAdmin;
