import React, { useEffect, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import cn from 'classnames';
import styles from './SearchPage.module.scss';
import PriceFilter from '../ProductsPage/PriceFilter';
import ProductsItem from '../../common/ProductsItem/ProductsItem';
import { Preloader } from 'components/common/Preloader/Preloader';
import { searchItem } from 'redux/actions/getProducts';
import { withTranslation } from 'react-i18next';
import OrderConfirmModal from 'components/common/OrderConfirmModal/OrderConfirmModal';
import Backdrop from 'components/common/Backdrop/Backdrop';
import AddToCartModal from 'components/common/AddToCartModal/AddToCartModal';
import ConfirmedModal from 'components/common/ConfirmedModal/ConfirmedModal';
import { createClient, cleanCart } from 'redux/actions/cart';
import { setLanguage } from 'redux/actions/language';

const SearchPage = ({ setLanguage, searchItem, loading, results, t, createClient, cleanCart, modalType, i18n }) => {
  let { title } = useParams();
  const searchItemCallback = useCallback(() => searchItem(title, i18n.language), [title, searchItem, i18n.language]);
  useEffect(() => {
    setLanguage(i18n.language);
  }, [setLanguage, i18n.language]);
  useEffect(() => {
    searchItemCallback();
  }, [searchItemCallback]);

  return (
    <>
      {loading ? (
        <>
          <Preloader />
        </>
      ) : (
        <section className={styles.wrapper}>
          <div className={cn('container')}>
            {!!results && results.length !== 0 ? (
              <>
                <PriceFilter />
                <div className={styles.productAlbum}>
                  {results.map(card => {
                    return <ProductsItem {...card} key={card._id} />;
                  })}
                </div>
              </>
            ) : (
              <div className={styles.notFoundResults}>{t('search.notFound')}</div>
            )}
          </div>
          {modalType === 'AddToCart' && <AddToCartModal />}
          {modalType === 'ConfirmOrder' && <OrderConfirmModal createClient={createClient} cleanCart={cleanCart} />}
          <ConfirmedModal />
          <Backdrop />
        </section>
      )}
    </>
  );
};

const mapStateToProps = state => {
  return {
    results: state.products.data,
    loading: state.products.loading,
    modalType: state.modal.modalType,
    language: state.language.local,
  };
};

export default withTranslation()(
  connect(mapStateToProps, { searchItem, createClient, cleanCart, setLanguage })(SearchPage)
);
