import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Home from 'components/pages/Home';
import Cart from 'components/pages/Cart';
import ProductsPage from '../components/pages/ProductsPage';
import ProductDetailsPage from 'components/pages/ProductDetailsPage';
import NotFoundPage from 'components/pages/NotFoundPage';
import SearchPage from 'components/pages/SearchPage';
import AdminPage from 'components/pages/AdminPage';

// import pages

const Routes = () => {
  return (
    <>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/cart" component={Cart} />
        <Route exact path="/products/:id" component={ProductsPage} />
        <Route path="/products/:id/:subId" component={ProductsPage} />
        <Route path="/item/:productId" component={ProductDetailsPage} />
        <Route path="/404" component={NotFoundPage} />
        <Route path="/search/:title" component={SearchPage} />
        <Route path="/admin" component={AdminPage} />
      </Switch>
    </>
  );
};
export default Routes;
