import React from 'react';
import { connect } from 'react-redux';
import styles from './BuyButton.module.scss';
import { withTranslation } from 'react-i18next';
import { addItem } from 'redux/actions/cart';
import { onModalOpen } from 'redux/actions/modal';
import cn from 'classnames';

const BuyButton = ({ data, addItem, onModalOpen, _id, focus, isMobile, t, upperPartBuyButton, reviewBuyButton }) => {
  return (
    <button
      onClick={() => {
        addItem(data);
        onModalOpen('AddToCart');
      }}
      className={cn(
        styles.cardButton,
        focus && focus.show && focus._id === _id && styles.cardButtonShow,
        isMobile && styles.cardButtonShow,
        upperPartBuyButton && styles.cardButtonShow,
        reviewBuyButton && styles.cardButtonShow
      )}
    >
      {t('buyBtn')}
    </button>
  );
};

export default withTranslation()(connect(null, { addItem, onModalOpen })(BuyButton));
