import { SET_REVIEW, SET_REVIEW_REQUEST, SET_REVIEW_FAILURE } from '../types/reviewItem';

const initialState = {
  data: [],
  error: '',
  loading: false,
};

const reviewItemReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_REVIEW:
      return {
        ...state,
        data: action.data,
        loading: false,
      };

    case SET_REVIEW_REQUEST:
      return {
        ...state,
        loading: true,
      };

    case SET_REVIEW_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.error,
      };

    default:
      return state;
  }
};

export default reviewItemReducer;
