import React, { useEffect, useState, useCallback } from 'react';
import AdminSubMenu from './AdminSubMenu';
import { connect } from 'react-redux';
import { Link, Route } from 'react-router-dom';
import styles from './AdminMenu.module.scss';
import buttonStyles from '../AdminPage.module.scss';
import editIcon from 'assets/icons/edit.svg';
import deleteIcon from 'assets/icons/clear-input.svg';
import { setCategoriesTrigger, deleteCategory, updateCategory, addSubCategory } from 'redux/actions/categories';
import { Preloader } from 'components/common/Preloader/Preloader';

const AdminMenu = ({
  deleteCategory,
  setCategoriesTrigger,
  updateCategory,
  addSubCategory,
  subCategorys,
  _id,
  name,
  catId,
  loading,
}) => {
  const setCategoriesTriggerCallback = useCallback(() => setCategoriesTrigger(), [setCategoriesTrigger]);

  useEffect(() => {
    setCategoriesTriggerCallback();
  }, [setCategoriesTriggerCallback]);

  const [editing, setEditing] = useState(false);
  const [newCategory, setNewCategory] = useState({
    ua: '',
    en: '',
    ru: '',
  });
  const [addMode, setAddMode] = useState(false);

  useEffect(() => {
    if (name.ua || name.en || name.ru) {
      setNewCategory(name);
    }
  }, [name]);

  const addNewSubCategory = () => {
    const newData = {
      name: {
        ua: newCategory.ua,
        en: newCategory.en,
        ru: newCategory.ru,
      },
      subCatId: Date.now(),
    };
    addSubCategory(_id, newData);
  };

  const setUpdate = () => {
    const newData = {
      name: {
        ua: newCategory.ua,
        en: newCategory.en,
        ru: newCategory.ru,
      },
      catId,
    };
    updateCategory(_id, newData);
  };

  const changeUaCategory = e => {
    setNewCategory({ ...newCategory, ua: e.target.value });
  };

  const changeEnCategory = e => {
    setNewCategory({ ...newCategory, en: e.target.value });
  };

  const changeRuCategory = e => {
    setNewCategory({ ...newCategory, ru: e.target.value });
  };

  const submitChanges = e => {
    setEditing(false);
    setUpdate();
    e.preventDefault();
  };

  const submitSubCategory = e => {
    setAddMode(false);
    addNewSubCategory();
    e.preventDefault();
  };

  return (
    <>
      {loading ? (
        <Preloader />
      ) : (
        <div className={styles.wrapper}>
          <div className={styles.mainBlock}>
            {!editing ? (
              <div className={styles.categoryBlock}>
                <div>
                  <h3 className={styles.editName}>{name.ua}</h3>
                  <h3 className={styles.editName}>{name.en}</h3>
                  <h3 className={styles.editName}>{name.ru}</h3>
                </div>

                <div>
                  <img src={editIcon} alt="edit" onClick={() => setEditing(true)} />
                </div>

                <div>
                  <img
                    src={deleteIcon}
                    alt="delete"
                    onClick={() => {
                      if (window.confirm(' ?')) {
                        deleteCategory(_id);
                      }
                    }}
                  />
                </div>

                <div className={styles.subCategoryLink}>
                  <Link to={`/admin/menu/${_id}`}> </Link>
                  <span>&#62;</span>
                </div>
              </div>
            ) : (
              <form onSubmit={submitChanges} className={styles.categoryForm}>
                <div>
                  <input
                    className={styles.editInput}
                    type="text"
                    placeholder="  ukr"
                    defaultValue={newCategory.ua}
                    onChange={changeUaCategory}
                    autoFocus
                  />
                  <input
                    className={styles.editInput}
                    type="text"
                    placeholder="  eng"
                    defaultValue={newCategory.en}
                    onChange={changeEnCategory}
                  />
                  <input
                    className={styles.editInput}
                    type="text"
                    placeholder="  rus"
                    defaultValue={newCategory.ru}
                    onChange={changeRuCategory}
                  />
                </div>

                <input className={styles.categorySubmit} type="submit" value="" />
              </form>
            )}

            <Route
              path={`/admin/menu/${_id}`}
              render={() => (
                <div className={styles.subCategoryBlock}>
                  <p className={styles.categoryHeading}>{subCategorys.length ? name.ua : null}</p>
                  {!!subCategorys &&
                    subCategorys.map(subItem => {
                      return <AdminSubMenu {...subItem} key={subItem._id} mainId={_id} />;
                    })}
                  <div>
                    {!addMode ? (
                      <button className={buttonStyles.addButton} onClick={() => setAddMode(true)}>
                          
                      </button>
                    ) : (
                      <form className={styles.categoryForm} onSubmit={submitSubCategory}>
                        <div>
                          <input
                            className={styles.editInput}
                            placeholder="  ukr"
                            onChange={changeUaCategory}
                            defaultValue=""
                            type="text"
                            autoFocus
                          />
                          <input
                            className={styles.editInput}
                            placeholder="  eng"
                            onChange={changeEnCategory}
                            defaultValue=""
                            type="text"
                          />
                          <input
                            className={styles.editInput}
                            placeholder="  rus"
                            onChange={changeRuCategory}
                            defaultValue=""
                            type="text"
                          />
                        </div>

                        <input type="submit" value="" className={styles.categorySubmit} />
                      </form>
                    )}
                  </div>
                </div>
              )}
            />
          </div>
        </div>
      )}
    </>
  );
};

const mapStateToProps = state => {
  return {
    loading: state.categories.loading,
  };
};

export default connect(mapStateToProps, { addSubCategory, deleteCategory, setCategoriesTrigger, updateCategory })(
  AdminMenu
);
