import React, { useState, useEffect, useCallback } from 'react';
import ProductClientPage from './ProductClientPage';
import ProductAdminPage from './ProductAdminPage';
import { connect } from 'react-redux';
import { getItem } from 'redux/actions/getItem';
import { useRouteMatch } from 'react-router-dom';

const ProductDetailsPageRender = ({ isAdmin, getItem, data }) => {
  const { params } = useRouteMatch();
  const { productId } = params;

  const getItemCallback = useCallback(() => {
    getItem(productId);
  }, [productId, getItem]);

  useEffect(() => {
    getItemCallback();
  }, [getItemCallback]);

  const { photos } = data;
  const [upperPhotosArr, setUpperPhotosArr] = useState();
  const [characteristicsArr, setCharacteristicsArr] = useState();
  const [clientArr, setClientArr] = useState();
  useEffect(() => {
    !!photos && setUpperPhotosArr(photos.filter(item => item.category === 'mainPhotos'));
    !!photos && setCharacteristicsArr(photos.filter(item => item.category === 'characteristics'));
    !!photos && setClientArr(photos.filter(item => item.category === 'client'));
  }, [photos]);

  const props = {
    isAdmin,
    data,
    upperPhotosArr,
    characteristicsArr,
    clientArr,
  };
  return <div>{isAdmin ? <ProductAdminPage {...props} /> : <ProductClientPage {...props} />}</div>;
};

const mapStateToProps = state => {
  return {
    data: state.productDetails.data,
    isAdmin: state.user.isAdmin,
  };
};

export default connect(mapStateToProps, { getItem })(ProductDetailsPageRender);
