import React from 'react';
import styles from './PriceFilter.module.scss';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import { sortByPriceHandler } from 'redux/actions/getProducts';

const PriceFilter = ({ sortByPriceHandler, priceFilter, t }) => {
  return (
    <div className={styles.filterBlock}>
      <p className={styles.filterLabel}>{t('priceFilter.title')}</p>

      <select value={priceFilter} onChange={e => sortByPriceHandler(e.target.value)} className={styles.filterSelect}>
        <option value={0}>{t('priceFilter.defaultOption')}</option>
        <option value={-1}>
          {t('priceFilter.price')} ({t('priceFilter.high')} &#60; {t('priceFilter.low')})
        </option>
        <option value={1}>
          {t('priceFilter.price')} ({t('priceFilter.low')} &#60; {t('priceFilter.high')})
        </option>
      </select>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    priceFilter: state.products.priceFilter,
  };
};

const mapDispatchToProps = {
  sortByPriceHandler,
};

export default withTranslation()(connect(mapStateToProps, mapDispatchToProps)(PriceFilter));
