import React, { useState, useEffect, useCallback } from 'react';
import styles from './PhotoItem.module.scss';
import { updatePhoto, deletePhoto } from 'redux/actions/gallery';
import { connect } from 'react-redux';

const PhotoItem = ({ photo, idItem, id, updatePhoto, deletePhoto, isAdmin }) => {
  const [photoItem, setBannerPhoto] = useState();

  const updatePhotoCallback = useCallback(() => {
    !!photoItem && updatePhoto({ idItem, id, photo: photoItem });
  }, [idItem, id, photoItem, updatePhoto]);

  useEffect(() => {
    updatePhotoCallback();
  }, [updatePhotoCallback]);

  const loadButtonRef = React.createRef();
  const newLoadButtonClick = () => loadButtonRef.current.click();
  const onPhotoSelect = async e => {
    setBannerPhoto(e.target.files[0]);
  };

  return (
    <div className={styles.mainPhoto}>
      <img src={photo} alt="main logo" className={styles.photoItem} />
      {!!isAdmin && (
        <div>
          <input className={styles.deleteIcon} type="button" onClick={() => deletePhoto(id)} />
          <input ref={loadButtonRef} style={{ display: 'none' }} type="file" onChange={onPhotoSelect} />
          <button onClick={newLoadButtonClick} type="button" className={styles.changePhoto}>
             
          </button>
        </div>
      )}
    </div>
  );
};

export default connect(null, { updatePhoto, deletePhoto })(PhotoItem);
