import React, { useState, useEffect } from 'react';
import styles from './PhotosBlock.module.scss';
import PhotoItem from 'components/pages/ProductDetailsPage/components/PhotoItem';

const PhotosBlock = ({ isAdmin, idItem, photos }) => {
  const [initialPhoto, setInitialPhoto] = useState();
  useEffect(() => {
    !!photos && !!photos[0] && setInitialPhoto(photos[0].photo);
  }, [photos]);

  const [templateColumnsCount, setTemplateColumnsCount] = useState(8);
  useEffect(() => {
    !!photos && setTemplateColumnsCount(Math.ceil(Math.sqrt(photos.length)));
  }, [photos]);

  return (
    <div className={styles.otherPhotos} style={{ gridTemplateColumns: `repeat(${templateColumnsCount * 2}, 1fr)` }}>
      {!!photos && !!photos[0] && (
        <div
          className={styles.bigPhoto}
          style={{
            gridColumn: `1 / span ${templateColumnsCount}`,
            gridRow: `1 / span ${templateColumnsCount}`,
          }}
        >
          {!!isAdmin ? (
            <PhotoItem isAdmin={isAdmin} idItem={idItem} id={photos[0]._id} photo={initialPhoto} />
          ) : (
            <div className={styles.photoBgcWrapper} style={{ backgroundImage: `url(${initialPhoto})` }}>
              {/* <img className={styles.blockBigImg} src={initialPhoto} alt="photoItem" /> */}
            </div>
          )}
        </div>
      )}
      {!!photos &&
        // !!(photos.length > 1) &&
        // photos.slice(1).map(img => {
        photos.map(img => {
          return (
            <div className={styles.smallPhoto} key={img._id}>
              {!!isAdmin ? (
                <PhotoItem
                  onClick={() => setInitialPhoto(img.photo)}
                  isAdmin={isAdmin}
                  idItem={idItem}
                  id={img._id}
                  photo={img.photo}
                />
              ) : (
                <div
                  className={styles.photoBgcWrapper}
                  style={{
                    backgroundImage: `url(${img.photo})`,
                    border: initialPhoto === img.photo && '2px solid #ccc',
                    boxSizing: 'border-box',
                  }}
                  onClick={() => setInitialPhoto(img.photo)}
                >
                  {/* <img
                    className={styles.blockImg}mg.photo
                    src={img.photo}
                    alt="photoItem"
                    onClick={() => setInitialPhoto(img.photo)}
                  /> */}
                </div>
              )}
            </div>
          );
        })}
    </div>
  );
};

export default PhotosBlock;
