import styles from './Characteristics.module.scss';
import React from 'react';
import { withTranslation } from 'react-i18next';

const CharacteristicIsNoAdmin = ({ photos, t }) => {
  return (
    <div className={styles.characteristicsWrapper}>
      <h2 className={styles.title}>{t('productDetailsPage.characteristics')}</h2>
      {!!photos &&
        photos.map(item => (
          <div className={styles.characteristicsDiv} key={item._id}>
            <img src={item.photo} alt="Product" />
          </div>
        ))}
    </div>
  );
};

export default withTranslation()(CharacteristicIsNoAdmin);
