import React from 'react';
import { connect } from 'react-redux';
import styles from './AdminFeedback.module.scss';
import { deleteReview } from 'redux/actions/reviewItem';
import StarRatings from 'react-star-ratings';
import { addReviewToItem } from 'redux/actions/reviewItem';

const AdminFeedback = ({
  name,
  text,
  date,
  deleteReview,
  _id,
  stars,
  photo,
  addReviewToItem,
  status,
  idItem,
  i18n,
  ...props
}) => {
  const addReviewToItemHandler = () => {
    const newData = {
      status: true,
    };
    addReviewToItem(_id, newData);
  };
  const deleteReviewFromItemHandler = () => {
    const newData = {
      status: false,
    };
    addReviewToItem(_id, newData);
  };
  return (
    <div className={styles.infoBlock}>
      <p className={styles.productTitle}>{!!idItem && idItem.title.ua}</p>
      <div className={styles.feedbackRate}>
        <div className={styles.userInfo}>
          <div className={styles.userInfoUp}>
            <div className={styles.userInfoLeft}>
              <p className={styles.username}>{name}</p>
              {stars > 0 && (
                <div>
                  <StarRatings
                    rating={stars ? +stars : 0}
                    numberOfStars={5}
                    starRatedColor="#FFDD64"
                    starEmptyColor="rgba(19, 19, 19, 0.1)"
                    starSpacing={'3px'}
                    starDimension={'20px'}
                  />
                </div>
              )}
            </div>
            <div>
              <p className={styles.feedbackDate}>{date}</p>
            </div>
          </div>

          <div className={styles.feedbackText}>
            <p>{text}</p>
          </div>

          {photo && (
            <div className={styles.imgContainer}>
              <img src={photo} alt="user" />
            </div>
          )}
        </div>
      </div>

      <div className={styles.buttonBlock}>
        <div className={styles.buttonSubBlock}>
          {status ? (
            <button className={styles.greyBtn} onClick={() => deleteReviewFromItemHandler()}>
                 
            </button>
          ) : (
            <button onClick={() => addReviewToItemHandler()}>   </button>
          )}

          <button
            className={styles.deleteButton}
            onClick={() => {
              if (window.confirm(' ?')) {
                deleteReview(_id);
              }
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default connect(null, { deleteReview, addReviewToItem })(AdminFeedback);
