import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import styles from './AdminMenu.module.scss';
import editIcon from 'assets/icons/edit.svg';
import deleteIcon from 'assets/icons/clear-input.svg';
import { deleteSubCategory, updateSubCategory } from 'redux/actions/categories';

const AdminSubMenu = ({ deleteSubCategory, updateSubCategory, name, _id, mainId, subCatId }) => {
  const [editing, setEditing] = useState(false);
  const [newSubCategory, setNewSubCategory] = useState({
    ua: '',
    en: '',
    ru: '',
  });

  useEffect(() => {
    if (name.ua || name.en || name.ru) {
      setNewSubCategory(name);
    }
  }, [name]);

  const setUpdate = () => {
    const newData = {
      name: {
        ua: newSubCategory.ua,
        en: newSubCategory.en,
        ru: newSubCategory.ru,
      },
      subCatId,
    };
    updateSubCategory(mainId, _id, newData);
  };

  const changeUaSubCategory = e => {
    setNewSubCategory({ ...newSubCategory, ua: e.target.value });
  };

  const changeEnSubCategory = e => {
    setNewSubCategory({ ...newSubCategory, en: e.target.value });
  };

  const changeRuSubCategory = e => {
    setNewSubCategory({ ...newSubCategory, ru: e.target.value });
  };

  const submitChanges = e => {
    setEditing(false);
    setUpdate();
    e.preventDefault();
  };

  return (
    <>
      {!editing ? (
        <div className={styles.subCategoryNonEdit}>
          <div>
            <h3 className={styles.editName}>{name.ua}</h3>
            <h3 className={styles.editName}>{name.en}</h3>
            <h3 className={styles.editName}>{name.ru}</h3>
          </div>

          <div>
            <img src={editIcon} alt="edit" onClick={() => setEditing(true)} />
          </div>

          <div>
            <img
              src={deleteIcon}
              alt="delete"
              onClick={() => {
                if (window.confirm(' ?')) {
                  deleteSubCategory(mainId, _id);
                }
              }}
            />
          </div>
        </div>
      ) : (
        <form className={styles.categoryForm} onSubmit={submitChanges}>
          <div>
            <input
              className={styles.editInput}
              defaultValue={newSubCategory.ua}
              type="text"
              onChange={changeUaSubCategory}
              autoFocus
            />
            <input
              className={styles.editInput}
              defaultValue={newSubCategory.en}
              type="text"
              onChange={changeEnSubCategory}
            />
            <input
              className={styles.editInput}
              defaultValue={newSubCategory.ru}
              type="text"
              onChange={changeRuSubCategory}
            />
          </div>

          <input className={styles.categorySubmit} type="submit" value="" />
        </form>
      )}
    </>
  );
};

export default connect(null, { deleteSubCategory, updateSubCategory })(AdminSubMenu);
