import React, { useEffect } from 'react';
import styles from './AddToCartModal.module.scss';
import { Button } from 'reactstrap';
import doneLogo from '../../../assets/icons/done.svg';
import { connect } from 'react-redux';
import { onModalClose, onModalOpen } from 'redux/actions/modal';
import { totalCountSelector } from 'redux/selectors/totalCountSelector';
import { totalPriceSelector } from 'redux/selectors/totalPriceSelector';
import { withTranslation } from 'react-i18next';
import { createClient, cleanCart } from 'redux/actions/cart';

const AddToCartModal = ({
  isOpen,
  modalType,
  onModalClose,
  totalCount,
  totalPrice,
  t,
  onModalOpen,
  createClient,
  cleanCart,
}) => {
  useEffect(() => {
    const handleEsc = e => {
      if (e.keyCode === 27) {
        onModalClose();
      }
    };
    window.addEventListener('keydown', handleEsc);

    return () => {
      window.removeEventListener('keydown', handleEsc);
    };
  }, [onModalClose]);

  return (
    <>
      {modalType === 'AddToCart' && (
        <div className={`${styles.modal} ${isOpen && styles.open} ${isOpen === false && styles.close}`}>
          <div>
            <div className={styles.modalTop}>
              <p>{t('addToCartModal.title')}</p>
              <Button close className={styles.closeButton} onClick={onModalClose} />
            </div>

            <div className={styles.modalMain}>
              <img src={doneLogo} alt={doneLogo} />

              <div>
                <p className={styles.modalText}>
                  {t('addToCartModal.totalCount')} {t('addToCartModal.item', { count: totalCount })}
                </p>
                <p className={styles.modalText}>
                  {t('navbar.cart')}{' '}
                  <b>
                    {totalPrice} {t('navbar.currency')}
                  </b>
                </p>
              </div>

              <div className={styles.buttonBlock}>
                <button className={styles.continueButton} onClick={onModalClose}>
                  {t('cart.buttons.continue')}
                </button>

                <button
                  className={styles.cartButton}
                  onClick={() => {
                    onModalClose();
                    onModalOpen('ConfirmOrder');
                  }}
                >
                  {t('cart.buttons.confirm')}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

const mapStateToProps = state => {
  return {
    isOpen: state.modal.isOpen,
    totalCount: totalCountSelector(state),
    totalPrice: totalPriceSelector(state),
    modalType: state.modal.modalType,
  };
};

export default withTranslation()(
  connect(mapStateToProps, { onModalClose, onModalOpen, createClient, cleanCart })(AddToCartModal)
);
