import React, { useState, useCallback } from 'react';
import styles from './Comments.module.scss';
import StarRatings from 'react-star-ratings';
import deleteIcon from 'assets/icons/delete.png';
import { withTranslation } from 'react-i18next';

let Comments = ({ reviews, isAdmin, t, isMobile }) => {
  const [countComments, setCountComments] = useState(isMobile ? 2 : 4);
  const handleShowMore = useCallback(() => setCountComments(isMobile ? countComments + 3 : countComments + 5), [
    countComments,
    isMobile,
  ]);
  const goodReviews = reviews.filter(item => item.status === true);
  const reviewsArr =
    goodReviews && goodReviews.length > 0
      ? goodReviews.map(r => {
          return (
            <div className={styles.reviews} key={r._id}>
              <div className={styles.info}>
                <div className={styles.userInfo}>
                  <div className={styles.nameBlock}>
                    <p>{r.name}</p>
                    <p className={styles.date}>{r.date}</p>
                  </div>

                  <div>
                    <StarRatings
                      rating={r.stars ? +r.stars : 0}
                      numberOfStars={5}
                      starRatedColor="#FFDD64"
                      starEmptyColor="rgba(19, 19, 19, 0.1)"
                      starSpacing={'3px'}
                      starDimension={'20px'}
                    />
                  </div>
                </div>
              </div>
              <div className={styles.text}>
                <p>{r.text}</p>
              </div>
              {r.photo.length !== 0 ? (
                <div className={styles.imgContainer}>
                  <img src={r.photo} alt="product" />
                </div>
              ) : null}
              <hr />
              {isAdmin && <img src={deleteIcon} className={styles.deleteIcon} alt="delete icon" />}
            </div>
          );
        })
      : null;

  return (
    <div className={styles.reviewsWrapper}>
      {!!reviewsArr && reviewsArr.filter((item, index) => index <= countComments)}
      {goodReviews.length >= countComments && (
        <button onClick={handleShowMore} className={styles.showMoreButton}>
          {t('commentForm.showMore')}
        </button>
      )}
    </div>
  );
};

export default withTranslation()(Comments);
