import React, { useEffect, useState, useCallback } from 'react';
import { Route, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import styles from './AdminPage.module.scss';
import AdminPhone from './AdminPhone';
import AdminMenu from './AdminMenu';
import AdminFeedBack from './AdminFeedback';
import NotFoundPage from 'components/pages/NotFoundPage/NotFoundPage';
import { addCategory } from 'redux/actions/categories';
import { getReview } from 'redux/actions/reviewItem';
import { Preloader } from 'components/common/Preloader/Preloader';

const AdminPage = ({ isAdmin, categories, addCategory, getReview, reviews, loading }) => {
  const getReviewCallback = useCallback(() => {
    getReview();
  }, [getReview]);
  const countNewReviews = reviews.filter(item => item.status === false).length;
  useEffect(() => {
    getReviewCallback();
  }, [getReviewCallback]);

  const [editing, setediting] = useState(false);
  const [newCategory, setNewCategory] = useState({
    ua: '',
    en: '',
    ru: '',
  });

  const setUpdate = () => {
    const newData = {
      name: {
        ua: newCategory.ua,
        en: newCategory.en,
        ru: newCategory.ru,
      },
      catId: Date.now(),
    };
    addCategory(newData);
  };

  const changeUaCategory = e => {
    setNewCategory({ ...newCategory, ua: e.target.value });
  };

  const changeEnCategory = e => {
    setNewCategory({ ...newCategory, en: e.target.value });
  };

  const changeRuCategory = e => {
    setNewCategory({ ...newCategory, ru: e.target.value });
  };

  const submitChanges = e => {
    setediting(false);
    setUpdate();
    e.preventDefault();
  };

  return (
    <>
      {isAdmin ? (
        <section className={styles.wrapper}>
          <div className="container">
            <div className={styles.adminTitle}> </div>

            <div className={styles.adminMain}>
              <div className={styles.adminMainLeft}>
                <ul>
                  <li className={styles.listItem}>
                    
                    <ul>
                      <li className={styles.listSubItem}>
                        <Link to="/admin/phone"></Link>
                      </li>

                      <li className={styles.listSubItem}>
                        <Link to="/admin/menu"></Link>
                      </li>
                    </ul>
                  </li>

                  <li className={styles.listItem}>
                    
                    <ul>
                      <li className={styles.listSubItem}>
                        <Link to="/admin/feedback"></Link>
                        {countNewReviews ? <sup>{countNewReviews}</sup> : null}
                      </li>
                    </ul>
                  </li>
                </ul>
              </div>

              <div className={styles.adminMainRight}>
                <div className={styles.subTitle}> </div>
                <Route path="/admin/phone" component={AdminPhone} />

                <Route
                  path="/admin/menu"
                  render={() => (
                    <>
                      {!!categories &&
                        categories.map(item => {
                          return <AdminMenu {...item} key={item._id} />;
                        })}

                      {!editing ? (
                        <button className={styles.addButton} onClick={() => setediting(true)}>
                            
                        </button>
                      ) : (
                        <form className={styles.categoryForm} onSubmit={submitChanges}>
                          <div>
                            <input
                              className={styles.editInput}
                              onChange={changeUaCategory}
                              placeholder="  ukr"
                              defaultValue=""
                              type="text"
                              autoFocus
                            />
                            <input
                              className={styles.editInput}
                              onChange={changeEnCategory}
                              placeholder="  eng"
                              defaultValue=""
                              type="text"
                            />
                            <input
                              className={styles.editInput}
                              onChange={changeRuCategory}
                              placeholder="  rus"
                              defaultValue=""
                              type="text"
                            />
                          </div>

                          <input type="submit" value="" className={styles.categorySubmit} />
                        </form>
                      )}
                    </>
                  )}
                />

                {loading ? (
                  <Preloader />
                ) : (
                  <Route
                    path="/admin/feedback"
                    render={() => {
                      return !!reviews && reviews.map(item => <AdminFeedBack {...item} key={item._id} />).reverse();
                    }}
                  />
                )}
              </div>
            </div>
          </div>
        </section>
      ) : (
        <NotFoundPage />
      )}
    </>
  );
};

const mapStateToProps = state => {
  return {
    isAdmin: state.user.isAdmin,
    categories: state.categories.data,
    reviews: state.review.data,
    loading: state.review.loading,
  };
};

export default connect(mapStateToProps, { addCategory, getReview })(AdminPage);
