import React, { useEffect } from 'react';
import styles from './SavedModal.module.scss';
import doneLogo from '../../../assets/icons/done.svg';
import { connect } from 'react-redux';
import { onModalClose } from 'redux/actions/modal';

const SavedModal = ({ isOpen, modalType, onModalClose }) => {
  useEffect(() => {
    const handleEsc = e => {
      if (e.keyCode === 27) {
        onModalClose();
      }
    };
    window.addEventListener('keydown', handleEsc);

    return () => {
      window.removeEventListener('keydown', handleEsc);
    };
  }, [onModalClose]);
  setTimeout(onModalClose, 1500);
  return (
    <>
      {modalType === 'SavedModal' && (
        <div className={`${styles.modal} ${isOpen && styles.open} ${isOpen === false && styles.close}`}>
          <div className={styles.modalMain}>
            <img src={doneLogo} alt={doneLogo} />
            <p className={styles.modalText}> !</p>
          </div>
        </div>
      )}
    </>
  );
};

const mapStateToProps = state => {
  return {
    isOpen: state.modal.isOpen,
    modalType: state.modal.modalType,
  };
};

export default connect(mapStateToProps, { onModalClose })(SavedModal);
