import React, { useState, useEffect, useCallback } from 'react';
import styles from './UpperPart.module.scss';
import StarRatings from 'react-star-ratings';
import { reduxForm, Field, formValueSelector } from 'redux-form';
import { connect } from 'react-redux';
import { addPhoto } from 'redux/actions/gallery';
import { getItem } from 'redux/actions/getItem';
import PhotoItem from 'components/pages/ProductDetailsPage/components/PhotoItem';

let UpperPartAdmin = ({
  handleSubmit,
  initialValues,
  updateStars,
  addPhoto,
  getItem,
  isAdmin,
  photos,
  categories,
  category,
  status,
  newStars,
  openModal,
}) => {
  const { _id, stars } = initialValues;
  const [newPhoto, addNewPhoto] = useState();

  const addPhotoCallback = useCallback(() => {
    !!newPhoto && addPhoto({ idItem: _id, photo: newPhoto, category: 'mainPhotos' });
  }, [newPhoto, _id, addPhoto]);

  useEffect(() => {
    addPhotoCallback();
  }, [addPhotoCallback]);

  const updatePageCallback = useCallback(() => {
    !!status && getItem(_id);
  }, [status, _id, getItem]);

  useEffect(() => {
    updatePageCallback();
  }, [updatePageCallback]);

  const loadButtonRef = React.createRef();
  const newLoadButtonClick = () => loadButtonRef.current.click();
  const onPhotoSelect = async e => {
    addNewPhoto(e.target.files[0]);
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <div className={styles.productInfo}>
          <div style={{ width: '50%' }}>
            {!!photos && !!photos[0] ? (
              <PhotoItem photo={photos[0].photo} idItem={_id} id={photos[0]._id} isAdmin />
            ) : (
              <div className={styles.addPhotoDiv}>
                <input ref={loadButtonRef} style={{ display: 'none' }} type="file" onChange={onPhotoSelect} />

                <button className={styles.addPhotoButton} type="button" onClick={newLoadButtonClick}>
                   
                </button>
              </div>
            )}
          </div>
          <div className={styles.description}>
            <div className={styles.idDiv}>
              <label>ID    LP-CRM:</label>
              <Field name="idLP" component="input" type="number" className={styles.cardTitle} />
            </div>
            <label>  UA:</label>
            <Field name="title.ua" component="input" type="text" className={styles.cardTitle} />
            <label>  EN:</label>
            <Field name="title.en" component="input" type="text" className={styles.cardTitle} />
            <label>  RU:</label>
            <Field name="title.ru" component="input" type="text" className={styles.cardTitle} />
            <div>
              <label>     UA:</label>
              <Field name="titleUpper.ua" component="input" type="text" className={styles.inputForMobile} />
              <label>     EN:</label>
              <Field name="titleUpper.en" component="input" type="text" className={styles.inputForMobile} />
              <label>     RU:</label>
              <Field name="titleUpper.ru" component="input" type="text" className={styles.inputForMobile} />
            </div>
            <div>
              <label>  UA:</label>
              <Field
                placeholder=" "
                name="titleLong.ua"
                component="input"
                type="text"
                className={styles.cardTitle}
              />
              <label>  EN:</label>
              <Field
                placeholder=" "
                name="titleLong.en"
                component="input"
                type="text"
                className={styles.cardTitle}
              />
              <label>  RU:</label>
              <Field
                placeholder=" "
                name="titleLong.ru"
                component="input"
                type="text"
                className={styles.cardTitle}
              />
            </div>
            <div>
              <StarRatings
                rating={newStars ? +newStars : !!stars ? stars : 0}
                numberOfStars={5}
                changeRating={updateStars}
                starRatedColor="#FFDD64"
                starEmptyColor="rgba(19, 19, 19, 0.1)"
                starHoverColor="yellow"
                starSpacing={'3px'}
                starDimension={'30px'}
              />
            </div>
            <div>
              <Field name="price" component="input" type="text" className={styles.price} /> 
            </div>
            <button type="submit" className={styles.buyButton}>
              
            </button>
          </div>
        </div>
        <div className={`${styles.otherPhotos} ${styles.imgWrapper}`}>
          <div className={styles.bigPhoto}>
            {!!photos && !!photos[1] ? (
              <PhotoItem photo={photos[1].photo} idItem={_id} id={photos[1]._id} isAdmin />
            ) : (
              <div className={styles.addPhotoDiv}>
                <input ref={loadButtonRef} style={{ display: 'none' }} type="file" onChange={onPhotoSelect} />

                <button className={styles.addPhotoButton} type="button" onClick={newLoadButtonClick}>
                   
                </button>
              </div>
            )}
          </div>
          {!!photos &&
            !!(photos.length > 2) &&
            photos.slice(2, 6).map(img => {
              return img ? (
                <div key={img._id}>
                  <PhotoItem photo={img.photo} idItem={_id} id={img._id} isAdmin />
                </div>
              ) : (
                <div className={styles.addPhotoDiv}>
                  <input ref={loadButtonRef} style={{ display: 'none' }} type="file" onChange={onPhotoSelect} />

                  <button className={styles.addPhotoButton} type="button" onClick={newLoadButtonClick}>
                     
                  </button>
                </div>
              );
            })}
          <div className={styles.addPhotoDiv}>
            <input ref={loadButtonRef} style={{ display: 'none' }} type="file" onChange={onPhotoSelect} />

            <button className={styles.addPhotoButton} type="button" onClick={newLoadButtonClick}>
               
            </button>
          </div>
        </div>
      </form>
    </div>
  );
};

const mapStateToProps = (state, props) => ({
  initialValues: state.productDetails.data,
  status: state.gallery.status,
  categories: state.categories.data,
});

const selector = formValueSelector('upperPartAdmin');
UpperPartAdmin = connect(state => {
  const category = selector(state, 'category');
  return {
    category,
  };
})(UpperPartAdmin);

export default connect(mapStateToProps, { addPhoto, getItem })(
  reduxForm({
    form: 'upperPartAdmin',
    enableReinitialize: true,
  })(UpperPartAdmin)
);
