import React, { useState, useEffect, useCallback, useRef } from 'react';
import styles from './Banner.module.scss';
import cn from 'classnames';
import { connect } from 'react-redux';
import { getMainInfo, addMainInfo, updateMainInfo } from 'redux/actions/mainInfo';
import { withTranslation } from 'react-i18next';
import { setOpenMenu } from 'redux/actions/menu';
import burger from 'assets/icons/burger.svg';

const Banner = ({
  isAdmin,
  title,
  titleLong,
  photo,
  _id,
  getMainInfo,
  addMainInfo,
  updateMainInfo,
  t,
  i18n,
  setOpenMenu,
}) => {
  const [editMode, setEditMode] = useState(false);
  const [bannerphoto, setBannerPhoto] = useState();
  const [bannerTitle_ua, setBannerTitle_ua] = useState();
  const [bannerTitle_ru, setBannerTitle_ru] = useState();
  const [bannerTitle_en, setBannerTitle_en] = useState();
  const [bannerSubtitle_ua, setBannerTitleLong_ua] = useState();
  const [bannerSubtitle_en, setBannerTitleLong_en] = useState();
  const [bannerSubtitle_ru, setBannerTitleLong_ru] = useState();
  // const [openMenu, setOpenMenu] = useState(false);

  const getMainInfoCallback = useCallback(() => getMainInfo(), [getMainInfo]);

  const updateMainInfoCallback = useCallback(() => {
    !!bannerphoto && !!_id
      ? updateMainInfo({
          id: _id,
          photo: bannerphoto,
        })
      : addMainInfo({
          photo: bannerphoto,
        });
  }, [bannerphoto, _id, updateMainInfo, addMainInfo]);

  useEffect(() => {
    getMainInfoCallback();
    !!bannerphoto && updateMainInfoCallback();
  }, [getMainInfoCallback, updateMainInfoCallback, bannerphoto]);

  useEffect(() => {
    !!title !== false && setBannerTitle_ua(title.ua);
    !!title !== false && setBannerTitle_en(title.en);
    !!title !== false && setBannerTitle_ru(title.ru);
    !!titleLong !== false && setBannerTitleLong_ua(titleLong.ua);
    !!titleLong !== false && setBannerTitleLong_en(titleLong.en);
    !!titleLong !== false && setBannerTitleLong_ru(titleLong.ru);
  }, [title, titleLong]);

  const setUpdate = () => {
    const newData = {
      title: { ua: bannerTitle_ua, en: bannerTitle_en, ru: bannerTitle_ru },
      titleLong: { ua: bannerSubtitle_ua, en: bannerSubtitle_en, ru: bannerSubtitle_ru },
    };
    const data = {
      'title.ua': bannerTitle_ua,
      'title.en': bannerTitle_en,
      'title.ru': bannerTitle_ru,
      'titleLong.ua': bannerSubtitle_ua,
      'titleLong.en': bannerSubtitle_en,
      'titleLong.ru': bannerSubtitle_ru,
      id: _id,
    };
    _id ? updateMainInfo(data) : addMainInfo(newData);
  };

  const loadButtonRef = useRef();
  const newLoadButtonClick = () => loadButtonRef.current.click();
  const onPhotoSelect = async e => {
    setBannerPhoto(e.target.files[0]);
  };

  const submitChanges = e => {
    setUpdate();
    setEditMode(false);
    e.preventDefault();
  };

  return (
    <>
      {/* {<Navbar isMobile openMenu={openMenu}/>} */}
      <div className={styles.wrapper} style={{ backgroundImage: `url(${photo})` }}>
        <div className={cn('container', styles.content)}>
          {!editMode ? (
            <div className={styles.content__inner}>
              {title && (
                <>
                  <h1 className={`${styles.title} ${styles.text}`}>
                    {title.ua && i18n.language === 'uk' && title.ua}
                    {title.ru && i18n.language === 'ru' && title.ru}
                    {title.en && i18n.language === 'en' && title.en}
                  </h1>
                  <h3 className={`${styles.subtitle} ${styles.text}`}>
                    {titleLong.ua && i18n.language === 'uk' && titleLong.ua}
                    {titleLong.ru && i18n.language === 'ru' && titleLong.ru}
                    {titleLong.en && i18n.language === 'en' && titleLong.en}
                  </h3>
                </>
              )}
              <button onClick={() => setOpenMenu(true)} className={`${styles.filterButton} ${styles.text}`}>
                <img src={burger} alt="burger" />
                FILTER
              </button>

              {isAdmin && (
                <button onClick={() => setEditMode(true)} className={styles.editBtn}>
                  
                </button>
              )}
            </div>
          ) : (
            <form className={cn('container', styles.form)} onSubmit={submitChanges}>
              <label> UA:</label>
              <input value={bannerTitle_ua} onChange={e => setBannerTitle_ua(e.target.value)} type="text" />
              <label> EN:</label>
              <input value={bannerTitle_en} onChange={e => setBannerTitle_en(e.target.value)} type="text" />
              <label> RU:</label>
              <input value={bannerTitle_ru} onChange={e => setBannerTitle_ru(e.target.value)} type="text" />
              <label> UA:</label>
              <textarea value={bannerSubtitle_ua} onChange={e => setBannerTitleLong_ua(e.target.value)} />
              <label> EN:</label>
              <textarea value={bannerSubtitle_en} onChange={e => setBannerTitleLong_en(e.target.value)} />
              <label> RU:</label>
              <textarea value={bannerSubtitle_ru} onChange={e => setBannerTitleLong_ru(e.target.value)} />
              <input type="submit" value=" " />
            </form>
          )}
        </div>

        {isAdmin && (
          <div>
            <input ref={loadButtonRef} style={{ display: 'none' }} type="file" onChange={onPhotoSelect} />
            <button onClick={newLoadButtonClick} type="button" className={styles.updateBtn}>
               
            </button>
          </div>
        )}
      </div>
    </>
  );
};

const mapStateToProps = state => {
  return {
    title: state.mainInfo.data.title,
    titleLong: state.mainInfo.data.titleLong,
    photo: state.mainInfo.data.photo,
    _id: state.mainInfo.data._id,
    isAdmin: state.user.isAdmin,
  };
};

export default withTranslation()(
  connect(mapStateToProps, { getMainInfo, addMainInfo, updateMainInfo, setOpenMenu })(Banner)
);
