import React, { useEffect, useCallback } from 'react';
import { useRouteMatch } from 'react-router-dom';
import ProductsItem from 'components/common/ProductsItem/ProductsItem';
import styles from './ProductsPage.module.scss';
import { connect } from 'react-redux';
import PriceFilter from './PriceFilter';
import { fetchProductsTrigger } from 'redux/actions/getProducts';
import Pagination from 'components/common/Pagination/Pagination';
import AddToCartModal from 'components/common/AddToCartModal/AddToCartModal';
import Backdrop from 'components/common/Backdrop/Backdrop';
import { Preloader } from 'components/common/Preloader/Preloader';
import { withTranslation } from 'react-i18next';
import { onModalOpen } from 'redux/actions/modal';
import { createClient, cleanCart } from 'redux/actions/cart';
import AddItemModal from 'components/common/AddItemModal';
import OrderConfirmModal from 'components/common/OrderConfirmModal/OrderConfirmModal';
import ConfirmedModal from 'components/common/ConfirmedModal/ConfirmedModal';

const ProductsPage = ({
  products,
  fetchProductsTrigger,
  currentPage,
  priceFilter,
  onModalOpen,
  isAdmin,
  loading,
  modalType,
  t,
  i18n,
}) => {
  const { params } = useRouteMatch();
  const { id, subId } = params;

  const fetchProductsTriggerCallback = useCallback(() => {
    fetchProductsTrigger(id, subId, currentPage, priceFilter);
  }, [id, subId, currentPage, priceFilter, fetchProductsTrigger]);
  useEffect(() => {
    fetchProductsTriggerCallback();
  }, [fetchProductsTriggerCallback]);

  return (
    <section className={styles.wrapper}>
      {loading ? (
        <Preloader />
      ) : (
        <>
          <div className="container">
            {products && products.length ? (
              <div>
                <div className={styles.topBlock}>
                  {products
                    .map(item => {
                      return (
                        <div className={styles.titleBlock} key={item._id}>
                          {!!item.categoryName && (
                            <h3 className={styles.categoryTitle}>
                              {i18n.language === 'uk' && item.categoryName.ua}
                              {i18n.language === 'ru' && item.categoryName.ru}
                              {i18n.language === 'en' && item.categoryName.en}
                            </h3>
                          )}

                          {subId
                            ? !!item.subCategoryName && (
                                <h3>
                                  {i18n.language === 'uk' && item.subCategoryName.ua}
                                  {i18n.language === 'ru' && item.subCategoryName.ru}
                                  {i18n.language === 'en' && item.subCategoryName.en}
                                </h3>
                              )
                            : null}
                        </div>
                      );
                    })
                    .slice(0, 1)}

                  <PriceFilter />

                  {isAdmin && (
                    <>
                      <button className={styles.addButton} onClick={() => onModalOpen('AddItemModal')}>
                         
                      </button>
                      <AddItemModal />
                    </>
                  )}
                </div>

                <div className={styles.productAlbum}>
                  {products.map(card => (
                    <ProductsItem {...card} key={card._id} catId={id} subCatId={subId} />
                  ))}
                </div>
                <Pagination />
              </div>
            ) : (
              <>
                {isAdmin && (
                  <div className={styles.addButtonNoProducts}>
                    <button className={styles.addButton} onClick={() => onModalOpen('AddItemModal')}>
                       
                    </button>
                  </div>
                )}
                <AddItemModal />
                <p style={{ textAlign: 'center' }}>{t('productsPage.emptyCategory')}</p>
              </>
            )}
          </div>
          {modalType === 'AddToCart' && <AddToCartModal />}
          {modalType === 'ConfirmOrder' && <OrderConfirmModal createClient={createClient} cleanCart={cleanCart} />}
          <ConfirmedModal />
          <Backdrop />
        </>
      )}
    </section>
  );
};

const mapStateToProps = state => {
  return {
    products: state.products.data,
    currentPage: state.products.currentPage,
    priceFilter: state.products.priceFilter,
    isAdmin: state.user.isAdmin,
    loading: state.products.loading,
    modalType: state.modal.modalType,
  };
};

export default withTranslation()(
  connect(mapStateToProps, { fetchProductsTrigger, onModalOpen, createClient, cleanCart })(ProductsPage)
);
