import React, { useEffect } from 'react';
import styles from './FeedbackSentModal.module.scss';
import doneLogo from '../../../assets/icons/done.svg';
import { withTranslation } from 'react-i18next';

import { connect } from 'react-redux';
import { onModalClose } from 'redux/actions/modal';

const FeedbackSentModal = ({ onModalClose, isOpen, modalType, t, confirmReview }) => {
  useEffect(() => {
    const handleEsc = e => {
      if (e.keyCode === 27) {
        onModalClose();
      }
    };
    window.addEventListener('keydown', handleEsc);

    return () => {
      window.removeEventListener('keydown', handleEsc);
    };
  }, [onModalClose]);
  return (
    <>
      {modalType === 'FeedbackSent' && (
        <div className={`${styles.modal} ${isOpen && styles.open} ${isOpen === false && styles.close}`}>
          <div>
            <div className={styles.modalTop}>
              <p>{t('confirmedModal.title')}</p>
            </div>

            <div className={styles.modalMain}>
              <img src={doneLogo} alt={doneLogo} />
              <div>
                <p className={styles.modalText}>{t('feedbackSentModal.review')}</p>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

const mapStateToProps = state => {
  return {
    isOpen: state.modal.isOpen,
    modalType: state.modal.modalType,
  };
};

export default withTranslation()(connect(mapStateToProps, { onModalClose })(FeedbackSentModal));
