import React, { useState, useEffect, useCallback } from 'react';
import { connect } from 'react-redux';
import { addPhoto } from 'redux/actions/gallery';
import styles from './AddPhotoButton.module.scss';

const AddPhotoButton = ({ idItem, category, addPhoto }) => {
  const [newPhoto, addNewPhoto] = useState();

  const addPhotoCallback = useCallback(() => {
    !!newPhoto && addPhoto({ idItem, photo: newPhoto, category });
  }, [newPhoto, idItem, category, addPhoto]);

  useEffect(() => {
    addPhotoCallback();
  }, [addPhotoCallback]);

  const loadButtonRef = React.createRef();
  const newLoadButtonClick = () => loadButtonRef.current.click();
  const onPhotoSelect = async e => {
    addNewPhoto(e.target.files[0]);
  };

  return (
    <div>
      <input ref={loadButtonRef} style={{ display: 'none' }} type="file" onChange={onPhotoSelect} />
      <button onClick={newLoadButtonClick} type="button" className={styles.changePhoto}>
         
      </button>
    </div>
  );
};

export default connect(null, { addPhoto })(AddPhotoButton);
