import React, { useState } from 'react';
import { useRouteMatch } from 'react-router-dom';
import { connect } from 'react-redux';
import ProductsItemLogo from './ProductsItemLogo';
import styles from './ProductsItem.module.scss';
import editIcon from 'assets/icons/edit.svg';
import deleteIcon from 'assets/icons/deleteItem.svg';
import { useMediaQuery } from 'react-responsive';
import { Link } from 'react-router-dom';
import { deleteProductsTrigger, addToTopSales, deleteFromTopSales } from 'redux/actions/getProducts';
import BuyButton from './BuyButton/BuyButton';
import { withTranslation } from 'react-i18next';

const ProductsItem = ({
  title,
  idLP,
  _id,
  catId,
  subCatId,
  currentPage,
  priceFilter,
  price,
  titleLong,
  photos,
  deleteProductsTrigger,
  isAdmin,
  isPopular,
  addToTopSales,
  deleteFromTopSales,
  t,
  i18n,
}) => {
  const mainPhotos = photos.filter(item => item.category === 'mainPhotos');

  const data = {
    idLP,
    title,
    _id,
    price,
    photo: mainPhotos[0].photo,
  };
  const { url } = useRouteMatch();

  const [focus, setFocus] = useState({ show: false, _id: null });

  const addToMainPage = () => {
    const newData = {
      isPopular: true,
    };
    addToTopSales(_id, newData, catId, subCatId, currentPage, priceFilter);
  };

  const deleteFromMainPage = () => {
    const newData = {
      isPopular: false,
    };
    deleteFromTopSales(_id, newData);
  };

  const deleteFromSales = () => {
    const newData = {
      isPopular: false,
    };
    addToTopSales(_id, newData, catId, subCatId, currentPage, priceFilter);
  };

  const isMobile = useMediaQuery({ query: '(max-width: 768px)' });

  return (
    <div
      className={styles.card}
      onMouseEnter={() => setFocus({ show: true, _id })}
      onMouseLeave={() => setFocus({ show: false, _id: null })}
    >
      <Link to={'/item/' + _id}>
        {!!mainPhotos[0] ? (
          <ProductsItemLogo key={mainPhotos[0]._id} focus={focus} _id={_id} mainPhoto={mainPhotos[0].photo} />
        ) : (
          <div className={styles.noImgDiv}></div>
        )}
      </Link>

      {isAdmin && url !== '/' && (
        <div className={styles.iconBlock}>
          <Link to={'/item/' + _id}>
            <img src={editIcon} alt="edit" className={styles.editIcon} />
          </Link>

          <img
            src={deleteIcon}
            alt="delete"
            className={styles.deleteIcon}
            onClick={() => {
              if (window.confirm(' ?')) {
                deleteProductsTrigger(_id, catId, subCatId, currentPage, priceFilter);
              }
            }}
          />
        </div>
      )}

      <div className={styles.cardTextBlock}>
        <p className={styles.cardName}>
          {i18n.language === 'uk' && title.ua}
          {i18n.language === 'ru' && title.ru}
          {i18n.language === 'en' && title.en}
        </p>

        <p className={styles.cardText}>
          {i18n.language === 'uk' && titleLong.ua}
          {i18n.language === 'ru' && titleLong.ru}
          {i18n.language === 'en' && titleLong.en}
        </p>
        <p className={styles.cardPrice}>
          <span className={styles.cardPriceBold}>{price}</span>
          &nbsp;{t('productsItem.currency')}
        </p>

        {!isAdmin ? (
          <BuyButton data={data} focus={focus} _id={_id} isMobile={isMobile} />
        ) : (
          <>
            {!isPopular ? (
              <button
                onClick={() => addToMainPage()}
                className={`
                          ${styles.cardButton} 
                          ${focus.show && focus._id === _id && styles.cardButtonShow}
                          ${isMobile && styles.cardButtonShow}
                      `}
              >
                   
              </button>
            ) : (
              <button
                onClick={url === '/' ? () => deleteFromMainPage() : () => deleteFromSales()}
                className={`
                            ${styles.cardButton} 
                            ${focus.show && focus._id === _id && styles.cardButtonShow}
                            ${isMobile && styles.cardButtonShow}
                        `}
              >
                   
              </button>
            )}
          </>
        )}
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    isAdmin: state.user.isAdmin,
    currentPage: state.products.currentPage,
    priceFilter: state.products.priceFilter,
  };
};

export default withTranslation()(
  connect(mapStateToProps, {
    deleteProductsTrigger,
    addToTopSales,
    deleteFromTopSales,
  })(ProductsItem)
);
