import React, { useState, useRef } from 'react';
import { connect } from 'react-redux';
import styles from './ComentForm.module.scss';
import StarRatings from 'react-star-ratings';
import Backdrop from 'components/common/Backdrop/Backdrop';
import FeedbackSentModal from 'components/common/FeedbackSentModal';
import { postReview } from 'redux/actions/reviewItem';
import { onModalClose, onModalOpen } from 'redux/actions/modal';
import { withTranslation } from 'react-i18next';
import deleteIcon from 'assets/icons/delete.png';

const CommentForm = ({ _id, postReview, onModalClose, onModalOpen, t }) => {
  const [newName, setNewName] = useState();
  const [newStars, setNewStars] = useState(0);
  const [newText, setNewText] = useState();
  const [newPhoto, setNewPhoto] = useState();
  const [fileExist, setFileExist] = useState(false);
  const fileref = useRef();
  const sendReview = () => {
    const newData = {
      idItem: _id,
      name: newName,
      stars: newStars,
      text: newText,
      photo: newPhoto,
      quality: 50,
    };
    postReview(newData);
  };

  const changeName = e => {
    setNewName(e.target.value);
  };

  const changeText = e => {
    setNewText(e.target.value);
  };

  const onPhotoSelect = async e => {
    setNewPhoto(e.target.files[0]);
    setFileExist(true);
  };

  const submitChanges = e => {
    sendReview();
    const form = e.target;
    onModalOpen('FeedbackSent');
    setTimeout(() => onModalClose(), 2000);
    e.preventDefault();
    form.reset();
    setNewStars(0);
    setFileExist(false);
  };
  return (
    <>
      <div className={styles.commentForm}>
        <h2>{t('commentForm.title')}</h2>
        <form onSubmit={submitChanges}>
          <div className={styles.reviewInfo}>
            <input onChange={changeName} type="text" placeholder={t('commentForm.name')} name="name" required />
          </div>

          <div className={styles.textReviews}>
            <input onChange={changeText} type="text" placeholder={t('commentForm.feedback')} name="comment" required />
          </div>

          <div className={styles.loadPhotoBlock}>
            <input
              ref={fileref}
              style={{ width: 'auto', border: 'none', color: 'rgba(19, 19, 19, 0.2)' }}
              type="file"
              onChange={onPhotoSelect}
            />
            {fileExist && (
              <img
                onClick={() => {
                  fileref.current.value = '';
                  setFileExist(false);
                }}
                src={deleteIcon}
                className={styles.deleteIcon}
                alt="delete icon"
              />
            )}
          </div>
          <div>
            {t('commentForm.rate')}:&nbsp;&nbsp;
            <StarRatings
              rating={newStars}
              numberOfStars={5}
              changeRating={setNewStars}
              starRatedColor="#FFDD64"
              starEmptyColor="rgba(19, 19, 19, 0.1)"
              starHoverColor="yellow"
              starSpacing={'3px'}
              starDimension={'30px'}
            />
          </div>

          <div className={styles.buttonContainer}>
            <button type="submit">{t('commentForm.send')}</button>
          </div>
        </form>
      </div>

      <FeedbackSentModal />

      <Backdrop />
    </>
  );
};

export default withTranslation()(connect(null, { postReview, onModalClose, onModalOpen })(CommentForm));
