import { SET_REVIEW, SET_REVIEW_REQUEST, SET_REVIEW_FAILURE } from '../types/reviewItem';
import { itemReviewAPI } from 'api/api';

const setReview = data => {
  return {
    type: SET_REVIEW,
    data,
  };
};

const setReviewRequest = () => {
  return {
    type: SET_REVIEW_REQUEST,
  };
};

const setReviewFailure = error => {
  return {
    type: SET_REVIEW_FAILURE,
    error,
  };
};

export const getReview = () => {
  return async dispatch => {
    dispatch(setReviewRequest());

    try {
      const data = await itemReviewAPI.getReview();
      dispatch(setReview(data));
    } catch (error) {
      dispatch(setReviewFailure(error));
    }
  };
};

export const postReview = newData => {
  return async dispatch => {
    try {
      await itemReviewAPI.addReview(newData);
      const data = await itemReviewAPI.getReview();
      dispatch(setReview(data));
    } catch (error) {
      dispatch(setReviewFailure(error));
    }
  };
};

export const addReviewToItem = (id, newData) => {
  return async dispatch => {
    dispatch(setReviewRequest());

    try {
      await itemReviewAPI.updateReview(id, newData);
      const data = await itemReviewAPI.getReview();
      dispatch(setReview(data));
    } catch (error) {
      dispatch(setReviewFailure(error));
    }
  };
};

export const deleteReview = id => {
  return async dispatch => {
    dispatch(setReviewRequest());

    try {
      await itemReviewAPI.deleteReview(id);
      const data = await itemReviewAPI.getReview();
      dispatch(setReview(data));
    } catch (error) {
      dispatch(setReviewFailure(error));
    }
  };
};
