import React, { useState, useEffect } from 'react';
import styles from './Video.module.scss';
import deleteIcon from 'assets/icons/delete.png';
import { withTranslation } from 'react-i18next';

const Video = ({ isAdmin, video, updateVideo, t }) => {
  const [newVideo, addNewVideo] = useState();
  useEffect(() => {
    !!video && addNewVideo(video);
  }, [video]);
  return (
    <div className={styles.videoContainerWrapper}>
      <h2 className={styles.title}>{t('productDetailsPage.video')}</h2>
      {!!isAdmin && (
        <div className={styles.videoChangingDiv}>
          <input
            className={styles.input}
            type="text"
            defaultValue={newVideo}
            onChange={e => addNewVideo(e.target.value)}
          />
          <button onClick={() => updateVideo(newVideo)} className={styles.addButton}>
            {!!video ? ' ' : ' '}
          </button>
        </div>
      )}
      {!!video && (
        <div className={styles.video}>
          <div className={styles.videoWrapper}>
            <iframe
              width="1000px"
              height="auto"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              title="videoblock"
              src={video}
              frameBorder="0"
              allowFullScreen
            >
              Video
            </iframe>
            {isAdmin && (
              <img src={deleteIcon} className={styles.deleteIcon} alt="delete icon" onClick={() => updateVideo('')} />
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default withTranslation()(Video);
