import React from 'react';
import StarRatings from 'react-star-ratings';
import styles from './UpperPartMobile.module.scss';
import BuyButton from 'components/common/ProductsItem/BuyButton/BuyButton';
import { withTranslation } from 'react-i18next';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const UpperPartMobile = ({ data, photos, i18n }) => {
  const { idLP, title, titleLong, titleUpper, _id, price, stars } = data;

  const sendData = {
    idLP,
    title,
    _id,
    price,
    photo: !!photos && !!photos[0] && photos[0].photo,
  };

  const sliderMobileSettings = {
    dots: true,
    infinite: true,
    autoplay: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    swipeToSlide: true,
    adaptiveHeight: true,
  };

  const photosArr = !!photos && photos.map(item => <img src={item.photo} alt="photoItem" key={item._id} />);

  return (
    <div className={styles.mainPart}>
      {!!titleUpper && (
        <h2 className={styles.nameUpper}>
          {i18n.language === 'uk' && titleUpper.ua}
          {i18n.language === 'ru' && titleUpper.ru}
          {i18n.language === 'en' && titleUpper.en}
        </h2>
      )}
      <div className={styles.productInfo}>
        <Slider className={styles.productSlider} {...sliderMobileSettings}>
          {photosArr}
        </Slider>
        {!!title && (
          <p className={styles.cardTitle}>
            {i18n.language === 'uk' && title.ua}
            {i18n.language === 'ru' && title.ru}
            {i18n.language === 'en' && title.en}
          </p>
        )}
        {!!titleLong && (
          <p className={styles.cardTitleLong}>
            {i18n.language === 'uk' && titleLong.ua}
            {i18n.language === 'ru' && titleLong.ru}
            {i18n.language === 'en' && titleLong.en}
          </p>
        )}
        <div className={styles.stars}>
          {!!stars && (
            <StarRatings
              rating={stars ? +stars : 0}
              numberOfStars={5}
              starRatedColor="#FFDD64"
              starEmptyColor="rgba(19, 19, 19, 0.1)"
              starSpacing={'3px'}
              starDimension={'30px'}
            />
          )}
        </div>
        <div>
          <span className={styles.price}>{price}</span> 
        </div>
        <BuyButton _id={_id} data={sendData} upperPartBuyButton />
      </div>
      <div className={styles.otherPhotos}></div>
    </div>
  );
};

export default withTranslation()(UpperPartMobile);
