import React from 'react';
import styles from './UpperPart.module.scss';
import StarRatings from 'react-star-ratings';
import PhotoItem from 'components/pages/ProductDetailsPage/components/PhotoItem';
import BuyButton from 'components/common/ProductsItem/BuyButton/BuyButton';
import { withTranslation } from 'react-i18next';

import PhotosBlock from 'components/pages/ProductDetailsPage/components/PhotosBlock';

const UpperPartIsNoAdmin = ({ data, photos, t, i18n }) => {
  const { idLP, title, titleLong, _id, price, stars, description } = data;
  const sendData = {
    idLP,
    title,
    _id,
    price,
    photo: !!photos && !!photos[0] && photos[0].photo,
  };
  return (
    <div>
      <div className={styles.productInfo}>
        <div className={styles.mainPhotoClient}>
          {!!photos && !!photos[0] && <PhotoItem photo={photos[0].photo} idItem={_id} id={photos[0]._id} />}
        </div>
        <div className={styles.description}>
          <p className={styles.cardTitle}>
            {title && (
              <>
                {title.ua && i18n.language === 'uk' && title.ua}
                {title.ru && i18n.language === 'ru' && title.ru}
                {title.en && i18n.language === 'en' && title.en}
              </>
            )}
          </p>
          <p className={styles.cardTitle}>
            {titleLong && (
              <>
                {titleLong.ua && i18n.language === 'uk' && titleLong.ua}
                {titleLong.ru && i18n.language === 'ru' && titleLong.ru}
                {titleLong.en && i18n.language === 'en' && titleLong.en}
              </>
            )}
          </p>
          <div>
            {!!stars && (
              <StarRatings
                rating={stars ? +stars : 0}
                numberOfStars={5}
                starRatedColor="#FFDD64"
                starEmptyColor="rgba(19, 19, 19, 0.1)"
                starSpacing={'3px'}
                starDimension={'30px'}
              />
            )}
          </div>
          <div>
            <span className={styles.price}>{price}</span> 
          </div>

          <div className={styles.buyButtonContainer}>
            <BuyButton _id={_id} data={sendData} upperPartBuyButton />
          </div>

          <div className={styles.descriptionText}>
            {description && (
              <>
                {description.ua && i18n.language === 'uk' && description.ua}
                {description.ru && i18n.language === 'ru' && description.ru}
                {description.en && i18n.language === 'en' && description.en}
              </>
            )}
          </div>
        </div>
      </div>
      {!!photos && <PhotosBlock idItem={_id} photos={photos.slice(1, 5)} />}
    </div>
  );
};

export default withTranslation()(UpperPartIsNoAdmin);
