import React from 'react';
import { useMediaQuery } from 'react-responsive';
import PhotosBlock from 'components/pages/ProductDetailsPage/components/PhotosBlock';
import AddPhotoButton from '../../components/AddPhotoButton';
import styles from './ClientsPhotos.module.scss';
import { withTranslation } from 'react-i18next';
// import PhotoItem from 'components/pages/ProductDetailsPage/components/PhotoItem';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const ClientsPhotos = ({ isAdmin, idItem, photos, t }) => {
  const photosArr =
    !!photos &&
    photos.map(
      item =>
        !!item && (
          <div className={styles.slideContainer} key={item._id}>
            <img src={item.photo} alt="photoItem" />
          </div>
        )
    );
  // const photosArrAdmin =
  //   !!photos &&
  //   photos.map(item => !!item && <PhotoItem photo={item.photo} isAdmin={isAdmin} idItem={idItem} key={item._id} />);

  const isMobile = useMediaQuery({
    query: '(max-width: 568px)',
  });

  // const sliderSettings = {
  //   dots: false,
  //   infinite: true,
  //   speed: 500,
  //   slidesToShow: 2,
  //   slidesToScroll: 1,
  //   arrows: true,
  //   autoplay: true,
  // };

  const sliderMobileSettings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    adaptiveHeight: true,
    swipeToSlide: true,
    autoplay: true,
  };

  return (
    <div className={styles.clientsPhotosBlock}>
      <div className={styles.titleWrapper}>
        <h2>{t('productDetailsPage.clients')}</h2>
        {isAdmin && <AddPhotoButton idItem={idItem} category="client" />}
      </div>
      {!!isMobile ? (
        <Slider {...sliderMobileSettings}>{photosArr}</Slider>
      ) : (
        <>
          {/* {isAdmin ? ( */}
          <PhotosBlock isAdmin={isAdmin} idItem={idItem} photos={photos} />
          {/* ) : (
            <Slider className="desktop-slider" {...sliderSettings}>
              {photosArr}
            </Slider>
          )} */}
        </>
      )}
    </div>
  );
};

export default withTranslation()(ClientsPhotos);
